/* --- BYU Components styling --- */
byu-header a {
  color: unset;
}

byu-header #site-name {
  color: #fff;
}

byu-header:not([mobile-view]) a {
  color: #fff;
}

byu-header #site-name a:hover {
  color: #fff;
  text-decoration: none;
}

.p-gotham p, .p-gotham span, .p-gotham div, .p-gotham a {
  font-family: "Gotham A", "Gotham B", Arial, sans-serif;
}

.p-libreb p, .p-libreb span, .p-libreb div, .p-libreb a {
  font-family: "Libre Baskerville", Georgia, serif;
}

[slot="site-title"] p, [slot="site-title"] span, [slot="site-title"] div, [slot="site-title"] a,
#site-name p,
#site-name span,
#site-name div,
#site-name a {
  font-family: "Vitesse A", "Vitesse B", serif;
}

.subtitle.italic {
  font-style: italic;
}

byu-header[mobile-view] [slot="actions"] a {
  color: #002e5d;
  font-weight: 500;
}

byu-header[mobile-view] [slot="actions"] p {
  padding-bottom: 0;
  margin: 0;
}

[slot="actions"] div,
[slot="actions"] p {
  padding-bottom: 0;
  margin: 0 15px;
}
[slot="actions"] a:hover {
  text-decoration: none;
}
[slot="actions"].royal {
  background: #003DA5;
  /*width: 110px;*/
  padding: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
}
[slot="actions"].royal p,
[slot="actions"].royal div {
  margin: 0 5px;
}

.region.region-header-actions {
  display: flex;
}

/* -- override we need for byu footer component */
.secondary-footer [class*="column"] + [class*="column"]:last-child {
  float: none !important;
}

/* -------- search --------- */
/* -- from instructions on byu-search component for SIMPLE search -- */
byu-search {
  .element-invisible {
    display: none;
  }

  .form-submit {
    display: none;
  }

  .form-item, .form-actions {
    margin: 0px;
    max-height: 23px;
    padding: 0px;
    /* border-radius: 0px; */
  }
  input[type=search] {
    border-radius: 0px;
    height: 30px;
  }
}

