  /* ------------ */
  .h1-vitesse h1,
  .h2-vitesse h2,
  .h3-vitesse h3,
  .h4-vitesse h4,
  .h5-vitesse h5 {
    font-family: "Vitesse A", "Vitesse B", Georgia, serif;
  }

  .h1-gotham h1,
  .h2-gotham h2,
  .h3-gotham h3,
  .h4-gotham h4,
  .h5-gotham h5 {
    font-family: "Gotham A", "Gotham B", Arial, sans-serif;
  }

  .h1-sentinel h1,
  .h2-sentinel h2,
  .h3-sentinel h3,
  .h4-sentinel h4,
  .h5-sentinel h5 {
    font-family: "Sentinel A", "Sentinel B", "Vitesse A", "Vitesse B", Helvetica, serif;
  }

  .h1-navy h1, .h2-navy h2, .h3-navy h3, .h4-navy h4, .h5-navy h5 {
    color: #002e5d;
  }

  .h1-gray h1, .h2-gray h2, .h3-gray h3, .h4-gray h4, .h5-gray h5 {
    color: #666;
  }

  .h1-black h1, .h2-black h2, .h3-black h3, .h4-black h4, .h5-black h5 {
    color: #000;
  }

  h1 {
    font-size: 36px;
  }

  h2 {
    font-size: 29px;
  }

  h3 {
    font-size: 24px;
  }

  byu-menu.transparent {
    z-index: 100;
  }

  #full-width-hero.hero_full_width_image_behind_menu {
    margin-top: -36px;
  }

  header#constrained-width-hero.hero_custom_width_behind_menu {
    margin-top: -36px;
  }

  .full-width-images > section img {
    width: 100%;
    height: auto !important;
  }

  .full-width-true div.main {
    max-width: 100%;
  }
  .full-width-false div.main {
    max-width: 1200px;
  }

  .main {
    padding: 30px;
    margin-left: auto;
    margin-right: auto;
  }
  .main.custom-width {
    padding: 30px 0px;
  }
  #page-container {
    padding: 20px 0;
  }

  /* Remove box-shadow from menu button in Firefox, Edge, IE11 */
  .mobile-menu-button {
    -webkit-box-shadow: none;
    box-shadow: none;
  }


  /* ---- Footer ---- */
  /*.three_four_merged .region-footer-41 {*/
  /*display: none;*/
  /*}*/
  /* - mobile */
  @media screen and (max-width: 767px) {
    byu-footer:not('normal') .two-column,
    byu-footer:not('normal') .three-column,
    byu-footer:not('normal') .four-column {
      column: 1;
    }
  }

  /* tablet */
  @media screen and (min-width: 768px) {
    .three_four_merged .region-footer-31,
    .two_three_merged .region-footer-21,
    .one_two_merged .region-footer-11 {
      width: 100%;
      padding-left: 0;
    }

  }

  /* large screen */
  @media screen and (min-width: 1200px) {
    .three_four_merged .region-footer-31,
    .two_three_merged .region-footer-21,
    .one_two_merged .region-footer-11 {
      width: 50%;
    }


  }

  /* ----------- Helpful Classess ---- */
  .two-column, .three-column, .four-column {
    -webkit-column-gap: 15px; /* Chrome, Safari, Opera */
    -moz-column-gap: 15px; /* Firefox */
    column-gap: 15px;
  }
  .two-column {
    column-count: 2;
    -webkit-column-gap: 30px; /* Chrome, Safari, Opera */
    -moz-column-gap: 30px; /* Firefox */
    column-gap: 30px;
  }
  .three-column {
    column-count: 3;
  }
  .four-column {
    column-count: 4;
  }

  .navy-button {
    background: #002e5d;
    color: #fff;
    padding: 12px 25px;
  }
  .medblue-button {
    background: #002e5d; /* change to medium blue */
    color: #fff;
    padding: 12px 25px;
  }



